import axios from '@/libs/api.request'
import store from '@/store'

const baseUrl = 'elections/';

export const getElectionList = ({current_page = 0, filter = {}, with: $with}) => {
    let data = {
        current_page,
        filter,
        with: $with //with是js保留词 所以加了$
    }
    data = Object.assign(
        data,
        {
            token: store.state.common.token,
            access_token: store.state.common.access_token
        }
    );
    return axios.request({
        url: baseUrl,
        method: 'get',
        params: data,
    })
}

export const getUserElectionList = ({current_page = 0, filter = {}, with: $with}) => {
    let data = {
        current_page,
        filter: Object.assign(filter, {initiate_user_id: store.state.common.user_id}),
        with: $with //with是js保留词 所以加了$
    }
    return getElectionList(data);
}

export const getElection = (id) => {
    let data = {
        token: store.state.common.token,
        access_token: store.state.common.access_token
    };
    return axios.request({
        url: baseUrl + id,
        method: 'get',
        params: data,
    })
}

