<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <filter-component :fields="filterFields" @handleSearch="handleSearch"/>
        <div style="height: 10px"></div>
        <a-alert
                message="功能提示"
                description="选举列表。"
                type="info"
                showIcon
        />
        <div style="height: 10px"></div>
        <table-component
                ref="table"
                @handleOperate="handleOperate"
                :columns="columns"
                :request="tableRequest">
        </table-component>
    </div>
</template>

<script>
    import TopButtons from './components/TopButtons'
    import {getElectionList, deleteElection} from '@/api/election'
    import {getVoteList} from '@/api/vote'
    import tableComponent from '@/components/table'
    import filterComponent from '@/components/filter/Index'

    export default {
        components: {
            TopButtons, filterComponent, tableComponent,
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                ElectionId: 0,
                visible: false,
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '发起人',
                        dataIndex: 'initiate_user.name',
                        key: 'initiate_user.name',
                    },
                    {
                        title: '标题',
                        dataIndex: 'title',
                        key: 'title',
                    },
                    {
                        title: '是否公示',
                        dataIndex: 'is_publicized_format',
                        key: 'is_publicized_format',
                    },
                    {
                        title: '选举开始时间',
                        dataIndex: 'started_at',
                        key: 'started_at',
                    },
                    {
                        title: '选举结束时间',
                        dataIndex: 'ended_at',
                        key: 'ended_at',
                    },
                    {
                        title: '公示时间',
                        dataIndex: 'published_at',
                        key: 'published_at',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            check: {
                                visible: true,
                                disable: (row) => {
                                    if (!row.is_publicized) {
                                        return true;
                                    }
                                    if (!row.published_at) {
                                        return true;
                                    }
                                    let now = moment().valueOf() / 1000;
                                    if (row.ended_at < now && row.published_at < now) {
                                        return false;
                                    }
                                    return true;
                                },
                                title: '查看结果',
                            },
                            edit: {
                                visible: true,
                                disable: (row) => {
                                    let now = moment().valueOf() / 1000;
                                    if (row.started_at < now && row.ended_at > now) {
                                        return false;
                                    }
                                    return true;
                                },
                                title: '参与',
                            },
                        }
                    }
                ],
                tableRequest: getElectionList,
                filterFields: [
                    {label: '开始时间', field: 'start_timestamp'},
                    {label: '结束时间', field: 'end_timestamp'},
                    {label: '关键字', field: 'keyword'},
                ],
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleCheck(id) {
                this.$router.push({name: '选举查看', params: {id: id}})
            },
            handleAttend(id) {
                this.$router.push({name: '选举参加', params: {id: id}})
            },
            handleOperate(type, row) {
                switch (type) {
                    case 'check':
                        this.handleCheck(row.id);
                        break;
                    case 'edit':
                        this.handleAttend(row.id);
                        break;
                }
            },
        },
    }
</script>
